.player-grid {
	width: 100%;
	height: 100%;

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-content: center;
	gap: 2em;
}

.no-results-message {
	width: 100%;
	margin-top: 100px;

	font-size: 3rem;
	font-weight: 300;
	letter-spacing: 3px;
	text-align: center;
}

@media (min-width: 768px) and (max-width: 1439px) {
	.player-grid {
		gap: 1em;
	}
}

@media (max-width: 767px) {
	.player-grid {
		gap: 0.5em;
	}

	.no-results-message {
		font-size: 2rem;
	}
}
