.details {
	display: flex;
	justify-content: center;
}

.player-container {
	width: 75%;
	height: 100%;

	display: flex;
	flex-direction: column;
	gap: 2em;
}

.top {
	display: flex;
	gap: 2em;
}

.player-cover {
	width: 500px;
	height: 600px;

	object-position: center top;
	object-fit: cover;

	background: url(../assets/images/loader_gif.gif) center no-repeat;
	border-radius: 10px;

	box-shadow: var(--simple-shadow);
}

.player-details {
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 2em;
}

.info {
	display: flex;
	gap: 1em;
	align-items: center;
}

.label,
.info-text {
	font-size: 2rem;
	font-weight: bold;
	text-transform: uppercase;
	text-shadow: var(--simple-shadow);
	letter-spacing: 2px;
}

.info-text {
	color: var(--light-accent);
	font-weight: 300;
}

.bottom {
	display: flex;
	flex-direction: column;
	gap: 2em;
}

.player-abstract {
	display: flex;
	flex-direction: column;
	gap: 1em;
}

.abstract {
	font-size: 1.5rem;
	font-weight: 300;
	line-height: 1.25;

	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3; /* number of lines to show */
	line-clamp: 2;
	-webkit-box-orient: vertical;
}

.back-button {
	padding: 0.25em 0.5em;

	background-color: transparent;
	border: solid 3px var(--dark-accent);
	border-radius: 10px;

	font-size: 2rem;
	color: var(--light-main);
	text-transform: uppercase;

	transition: all 0.15s ease-in-out;
}

.back-button:hover {
	cursor: pointer;
	color: var(--dark-main);
	background-color: var(--light-main);
}

.back-button:focus {
	outline: none;
	border-color: var(--light-main);
}

/*LOADER CSS FOR THIS PAGE*/
.details .spinner-container {
	width: fit-content;
	margin-top: 0px;
	margin-left: 200px;
}

.details .spinner {
	width: 350px;
	height: 350px;
}

.details .loading-text {
	font-size: 1.5rem;
}

.details .spinner-sector {
	border-width: 15px;
}

@media (min-width: 768px) and (max-width: 1439px) {
	.details {
		align-items: center;
	}

	.player-container {
		width: 100%;
		height: 100%;
	}

	.top {
		display: flex;
		justify-content: space-evenly;
	}

	.player-cover {
		width: 40%;
		height: 400px;
	}

	.player-details {
		justify-content: space-between;
	}

	.info {
		width: 100%;
		gap: 0.5em;
	}

	.label,
	.info-text {
		font-size: 1.35rem;
	}

	.abstract {
		display: block;

		font-size: 1.5rem;
		line-height: 1.5;
		text-align: justify;
	}

	/*LOADER CSS FOR THIS PAGE*/
	.details .spinner-container {
		margin-left: unset;
	}
}

@media (max-width: 767px) {
	.details {
		align-items: center;
	}

	.player-container {
		width: 100%;
		height: 100%;
	}

	.top {
		display: flex;
		flex-direction: column;
		justify-content: unset;
		gap: 1em;
	}

	.player-cover {
		width: 100%;
		height: 200px;
	}

	.player-details {
		justify-content: unset;
		gap: 0.5em;
	}

	.info {
		width: 100%;
		justify-content: space-between;
	}

	.label,
	.info-text {
		font-size: 0.85rem;
	}

	.player-abstract {
		gap: 0.5em;
	}

	.abstract {
		display: block;

		font-size: 1rem;
		line-height: 1.5;
		text-align: justify;
	}

	.back-button {
		padding: 0.5em 1em;
		font-size: 1.25rem;
	}

	/*LOADER CSS FOR THIS PAGE*/
	.details .spinner-container {
		width: fit-content !important;
		margin-left: unset;
		margin: auto;
	}

	.details .spinner {
		width: 200px !important;
		height: 200px !important;
	}

	.details .loading-text {
		font-size: 0.85rem !important;
	}

	.details .spinner-sector {
		border-width: 10px !important;
	}
}
