.player-card {
	width: 30%;
	height: 400px;

	background-color: var(--dark-accent);
	border-radius: var(--card-radius);
	box-shadow: var(--simple-shadow);

	display: flex;
	align-items: center;

	overflow: hidden;
	transition: all 0.15s ease-in-out;
}

.player-card:hover {
	cursor: pointer;
	opacity: 0.75;
	transform: scale(0.95);
}

.player-image {
	width: 50%;
	height: 100%;

	background: url(../assets/images/loader_gif.gif) center no-repeat;

	object-position: center top;
	object-fit: cover;
}

.player-info {
	width: 50%;
	padding: 0 0.25em;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 1em;

	text-align: center;
}

.player-title {
	font-size: 1.5rem;
	font-weight: bold;
	text-transform: uppercase;
}

.player-data {
	font-size: 1.25rem;
	font-weight: 300;
}

.hide {
	display: none;
}

@media (min-width: 768px) and (max-width: 1439px) {
	.player-card {
		height: 300px;
	}

	.player-info {
		gap: 0.5em;
	}

	.player-title {
		font-size: 1rem;
	}

	.player-data {
		font-size: 0.85rem;
	}
}

@media (max-width: 767px) {
	.player-card {
		width: 45%;
		height: 280px;

		flex-direction: column;
	}

	.player-image {
		width: 100%;
		height: 60%;
	}

	.player-info {
		width: 100%;
		height: 40%;
		padding: 0.15em 0;

		gap: 0.15em;
	}

	.player-title {
		font-size: 1rem;
	}

	.player-data {
		font-size: 0.75rem;
	}
}
